@value mediaQuery from '../../common/size.css';
@value makerWhite from '../../common/colors.css';
.maker{
  width: 100%;
  height: 100%;
  max-width: 80rem;
  display: flex;
  flex-direction: column;
  background-color: makerWhite;  
}

.container{
  display: flex;
  flex: 1;
  min-height: 0;
}

/* 스트린 사이즈가 작아지면 세로로 바꾸기 위해서 */
@media screen and (max-width: mediaQuery){
  .container{
    flex-direction: column;
  }
}